button {
	margin: 0;
	padding: 0;
	border: 0;
	background: none;
	font-size: 100%;
	vertical-align: baseline;
	font-family: inherit;
	font-weight: inherit;
	color: inherit;
	-webkit-appearance: none;
	appearance: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

button::-moz-focus-inner {
	border: none;
}

.footer {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 10px 15px;
	height: 20px;
	text-align: center;
	font-size: 15px;
	border-top: 1px solid #e6e6e6;
	color: gray;
}

.footer:before {
	content: '';
	position: absolute;
	z-index: -1;
	right: 0;
	bottom: 0;
	left: 0;
	height: 50px;
	overflow: hidden;
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2),
					0 8px 0 -3px #f6f6f6,
					0 9px 1px -3px rgba(0, 0, 0, 0.2),
					0 16px 0 -6px #f6f6f6,
					0 17px 2px -6px rgba(0, 0, 0, 0.2);
}

.todo-count {
	text-align: left;
}

.todo-count strong {
	font-weight: 300;
}

.clear-completed,
.clear-completed:active {
	position: relative;
	line-height: 20px;
	text-decoration: none;
	cursor: pointer;
}

.clear-completed:hover {
	text-decoration: underline;
}